.paragraphDecorators-dialog .preview {
    border-radius: 10px;
    padding: 20px;
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: whitesmoke;
    box-shadow: -3px 5px 10px -4px rgba(0,0,0,0.21);
}

.paragraphDecorators-dialog .preview_list {
    min-width: 300px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.paragraphDecorators-dialog .listPreviewBox {
    height: 100%;
}

.paragraphDecorators-dialog .preview-content {
    height: 220px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.paragraphDecorators-dialog .preview-content_withPointer {
    cursor: pointer;
}

.paragraphDecorators-dialog .preview-title {
    color: darkgrey;
    font-size: 12pt;
}

.paragraphDecorators-dialog .demonstrationElement {
    width: 100%;
    height: 92%;
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    word-break: break-word;
    overflow: hidden;
    border-radius: 7px;
    border: 1px solid lightgray;
    padding-left: 20px;
    padding-right: 20px;
}

.paragraphDecorators-dialog .demonstrationElement-text {
    display: flex;
    flex-flow: column;
    cursor: text;
    text-align: left;
    margin-left: 20px;
    overflow: visible;
    max-height: 100%;
}

.paragraphDecorators-dialog .demonstrationElement-text div {
    visibility: visible;
    visibility: initial;
    background-color: inherit;
    margin-top: inherit;
    margin-bottom: inherit;
}

.paragraphDecorators-dialog .demonstrationElement-text span {
    white-space: pre-wrap;
}

.paragraphDecorators-dialog .listItemBeginning {
    display: inline-block;
}

.aria {
    resize: none;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 100%;
    height: 100%;
    padding-top: 20%;
    padding-bottom: 20%;

}

.ariabox {
